import React, { Fragment, useState } from 'react';
import { Button, Col, Container, Modal, Row } from 'react-bootstrap';
import { FiPlayCircle } from 'react-icons/fi';
import ReactPlayer from 'react-player';
import '../../assets/css/style.scss';
function VideoSection() {
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    return (
            <Fragment>
                <section className="VideoSection">
                    <Container>
                        <Row className="justify-content-center align-items-center">
                            <Col lg={5}>
                                <div className="VideoBtn text-center">
                                    <a onClick={handleShow} ><FiPlayCircle/></a>
                                {/* Moadl Start */}
                                <Modal show={show} onHide={handleClose}>
                                    <Modal.Body>
                                        <ReactPlayer url='https://www.youtube.com/watch?v=ysz5S6PUM-U' style={{maxWidth:"100%"}} />
                                    </Modal.Body>
                                    <Modal.Footer>
                                        <Button variant="secondary" onClick={handleClose}>  Close </Button>
                                        <Button variant="primary" onClick={handleClose}> Save Changes </Button>
                                    </Modal.Footer>
                                </Modal>
                                {/* Modal End */}
                                </div>
                            </Col>
                            <Col lg={5}>
                                <div className="VideoSectionCont">
                                    <div className="SectionHeading">
                                        <p>Watch Video</p>
                                        <h2>Our Live Broadcast</h2>
                                    </div>
                                    <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Nulla quibusdam id sapiente saepe animi hic culpa beatae omnis dolore repellat cupiditate quam laudantium esse, at iusto, blanditiis in ad alias.</p>
                                </div>
                            </Col>
                        </Row>
                    </Container>
                </section>
            </Fragment>
    )
}

export default VideoSection;
