import React, { Component, Fragment } from 'react';
import { Button, Col, Container, FloatingLabel, Form, Row } from 'react-bootstrap';
import { FiDribbble, FiFacebook, FiInstagram, FiTwitter, FiYoutube } from 'react-icons/fi';
import '../assets/css/style.scss';
import AboutSection from '../components/AboutSection/AboutSection';
import CopyRight from '../components/CopyRightSection/CopyRight';
import FooterSection from '../components/FooterSection/FooterSection';
import Menu from '../components/mainMenu/Menu';
import PageTop from '../components/PageTop/PageTop';
import TopHeader from '../components/topheader/TopHeader';

export class ContactPage extends Component {
    componentDidMount(){
        window.scroll(0,0)
    }
    render() {
        return (
            <Fragment>
            <TopHeader title="Contact Page"/>
            <Menu/>
            <PageTop Title="Contact Page" Current="Conatc" link="/contact" />
            <div className="contactSection">
                <Container>
                    <Row className="jutify-content-center align-items-center">
                        <Col lg={5} md={6}>
                            <div className="contactDetails">
                                <h3>Get in touch</h3>
                                <h2>Write Us a<span>Short Massaege</span></h2>
                                <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Labore, iste blanditiis aperiam earum adipisci natus qui, animi quisquam illo perspiciatis at veritatis omnis odio cumque cupiditate vel vitae sapiente minus.</p>
                                <div className="contactLinks">
                                    <a href=""><FiFacebook/></a>
                                    <a href=""><FiTwitter/></a>
                                    <a href=""><FiInstagram/></a>
                                    <a href=""><FiYoutube/></a>
                                    <a href=""><FiDribbble/></a>
                                </div>
                            </div>
                        </Col>
                        <Col lg={7} md={6}>
                            <div className="contactForm">
                                    <Form>
                                        <Row className="py-lg-3 py-3">
                                            <Col lg={6} className="py-lg-0 py-3">
                                                <Form.Label>Frist Name</Form.Label>
                                                <Form.Control placeholder="First name" />
                                            </Col>
                                            <Col lg={6}>
                                                <Form.Label>Last Name</Form.Label>
                                                <Form.Control placeholder="Last name" />
                                            </Col>
                                        </Row>
                                        <Row className="py-lg-3 py-3">
                                            <Col lg={6} className="py-lg-0 py-3">
                                                <Form.Group as={Col} controlId="formGridEmail">
                                                    <Form.Label>Email</Form.Label>
                                                    <Form.Control type="email" placeholder="Enter email" />
                                                </Form.Group>
                                            </Col>
                                            <Col lg={6}>
                                                <Form.Label>Subject</Form.Label>
                                                <Form.Control placeholder="Subject" />
                                            </Col>
                                        </Row>
                                        <Col lg={12} className="py-lg-3 py-3">
                                            <FloatingLabel controlId="floatingTextarea2" label="Comments">
                                                <Form.Control
                                                as="textarea"
                                                placeholder="Leave a comment here"
                                                style={{ height: '100px' }}
                                                />
                                            </FloatingLabel>
                                        </Col>
                                        <Button className="formSendBtn" variant="primary" type="submit">Send Massaege</Button>
                                    </Form>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </div>
            <AboutSection/>
            <FooterSection/>
            <CopyRight/>
            </Fragment>
        )
    }
}

export default ContactPage
