import React, { Component, Fragment } from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { Helmet } from 'react-helmet';
import { FaFacebookSquare, FaInstagram, FaPinterestP, FaTwitter, FaYoutube } from 'react-icons/fa';
import '../../assets/css/style.scss';
export class TopHeader extends Component {
    render() {
        return (
            <Fragment id="TOP">
            <Helmet>
                <title>{this.props.title} </title>
                <meta name="description" content="Nested component" />
            </Helmet>
                <Container fluid className='bg-dark'>
                    <Container>
                        <Row className="py-3 text-light align-items-center">
                            <Col lg={7}>
                                <p>Better Sulution To Be Better Buisness <span>Free Subscribe</span></p>
                            </Col> 
                            <Col lg={3} className='text-end'>
                                <h3>453 Hight City Road st, USA</h3>
                            </Col>
                            <Col lg={2} align="center">
                                <div className="hederTopLinks">
                                    <a href="#"><FaFacebookSquare/></a>
                                    <a href="#"><FaTwitter/></a>
                                    <a href="#"><FaInstagram/></a>
                                    <a href="#"><FaPinterestP/></a>
                                    <a href="#"><FaYoutube/></a>
                                </div>
                            </Col>
                        </Row>
                    </Container>
                </Container>
            </Fragment>
        )
    }
}

export default TopHeader;
