import React, { Component, Fragment } from 'react';
import { Container, Nav, Navbar } from 'react-bootstrap';
import { GiFirePunch } from 'react-icons/gi';
import { NavLink } from 'react-router-dom';
import '../../assets/css/style.scss';
export class Menu extends Component {
    constructor(){
        super();
        this.state={
            withoutScroll:'mainMenu',
        }
    }
    onScroll=()=>{
        if(window.scrollY>100){
            this.setState({withoutScroll:'mainMenuFixed'})
        }
        else if(window.scrollY<100){
            this.setState ({withoutScroll:'mainMenu'})
        }
    }
    componentDidMount(){
        window.addEventListener('scroll',this.onScroll)
        window.scroll(0,0)
    }
    render() {
        return (
            <Fragment>
                <Navbar className={this.state.withoutScroll} bg="light" expand="lg">
                    <Container>
                            <Navbar.Brand>
                                <NavLink  className="mainBrand" exact to="/"><span><GiFirePunch/></span>M-Pair</NavLink>
                            </Navbar.Brand>
                                <Navbar.Toggle aria-controls="navbarScroll" />
                                <Navbar.Collapse id="navbarScroll">
                                    <Nav className="mainUl m-auto my-lg-0 align-items-center" navbarScroll >
                                    <NavLink exact activeStyle={{color:"red"}} className="nav-link" to="/">Home</NavLink>
                                    <NavLink exact activeClassName="ActiveLink" className="nav-link" to="/about">About</NavLink>
                                    <NavLink exact activeClassName="ActiveLink" className="nav-link" to="/service">Services</NavLink>
                                    <NavLink exact activeStyle={{color:"red"}} className="nav-link" to="/blog">Blog</NavLink>
                                    <Nav.Link href="#Shedule">Shedule</Nav.Link>
                                    <NavLink exact activeClassName="ActiveLink" className="nav-link" to="/contact">Contact</NavLink>
                                </Nav>
                                <a href="#" className="BtnCTB">Donate Now</a>
                            </Navbar.Collapse>
                    </Container>
                </Navbar>
            </Fragment>
        )
    }
}
export default Menu;
