import React, { Component, Fragment } from 'react';
import { Breadcrumb, Col, Container, Row } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';
import '../../assets/css/style.scss';
import PageBG from "../../assets/images/Hero/slider.jpg";
export class PageTop extends Component {
    render(props) {
        return (
            <Fragment>
                <div className="PageTopSection"style={{backgroundImage:`url(${PageBG})`}} >
                    <Container>
                        <Row className="justify-content-center">
                            <Col lg={4}>
                                <div className="PageTopCont text-center">
                                    <h1>{this.props.Title}</h1>
                                    <Breadcrumb>
                                        <Breadcrumb.Item className="justify-content-center"><NavLink to="/">Home</NavLink></Breadcrumb.Item>
                                        <Breadcrumb.Item active><NavLink to={this.props.link}>{this.props.Current}</NavLink></Breadcrumb.Item>
                                    </Breadcrumb>
                                </div>
                            </Col>
                        </Row>
                    </Container>
                </div>
                
            </Fragment>
        )
    }
}

export default PageTop
