import React, { Component, Fragment } from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { FaQuoteLeft, FaQuoteRight } from 'react-icons/fa';
import Slider from "react-slick";
import '../../assets/css/style.scss';
import Client1 from '../../assets/images/Testimonial/Client1.png';
import Client2 from '../../assets/images/Testimonial/Client2.png';
import Client3 from '../../assets/images/Testimonial/Client3.png';

export class Testimonial extends Component {
    constructor(props) {
        super(props);
        this.state = {
          nav1: null,
          nav2: null
        };
    }
    
    componentDidMount() {
        this.setState({
        nav1: this.slider1,
        nav2: this.slider2
        });
    }

    render() {
        return (
            <Fragment>
                <div className="TestimonialSection">
                    <Container>
                        <Row className="justify-content-end align-items-center">
                            <Col lg={5}>
                                <div className="Testimonial">
                                    <div className="SectionHeading">
                                        <p>Client Testimonial</p>
                                        <h2>Beuitifull Client Tell About US</h2>
                                    </div>
                                    <Slider 
                                        className='TestimonialSlider'
                                        asNavFor={this.state.nav2}
                                        arrows={false}
                                        ref={slider => (this.slider1 = slider)}>
                                        <div className="TestiQoute">
                                            <FaQuoteLeft className="QuoteLeft" />
                                            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Doloribus, provident maxime optio fugit non nesciunt iusto reprehenderit voluptatibus delectus nulla veniam libero?</p>
                                            <FaQuoteRight className="QuoteRight" />
                                        </div>
                                        <div className="TestiQoute">
                                            <FaQuoteLeft className="QuoteLeft" />
                                            <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Hic ullam dolorum sapiente nobis omnis! Consequatur delectus voluptate reprehenderit totam molestiae.</p>
                                            <FaQuoteRight className="QuoteRight" />
                                        </div>
                                        <div className="TestiQoute">
                                            <FaQuoteLeft className="QuoteLeft" />
                                            <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Eligendi ullam ab minus iure alias fugit aperiam ex deleniti?</p>
                                            <FaQuoteRight className="QuoteRight" />
                                        </div>
                                    </Slider>
                                    <Slider
                                        className='TestimonialClient'
                                        asNavFor={this.state.nav1}
                                        ref={slider => (this.slider2 = slider)}
                                        slidesToShow={1}
                                        swipeToSlide={true}
                                        centerPadding={'4rem'}
                                        fade={true}
                                        autoplay={true}
                                        autoplaySpeed={'3000'}
                                        centerMode={true}
                                        focusOnSelect={true}>
                                        <div className="TestClientInfo">
                                            <img src={Client1} className="ClientImg" alt="Slider Images" />
                                            <div className="TestClient">
                                                <h2>Mojibur Rahman</h2>
                                                <p>Camera Man</p>
                                            </div>
                                        </div>
                                        <div className="TestClientInfo">
                                            <img src={Client2} className="ClientImg" alt="Slider Images" />
                                            <div className="TestClient">
                                                <h2>Wilson Dated</h2>
                                                <p>Curiographer</p>
                                            </div>
                                        </div>
                                        <div className="TestClientInfo">
                                            <img src={Client3} className="ClientImg" alt="Slider Images" />
                                            <div className="TestClient">
                                                <h2>Rizur Polash</h2>
                                                <p>Buissness Man</p>
                                            </div>
                                        </div>
                                    </Slider>
                                </div>
                            </Col>
                        </Row>
                    </Container>
                </div>
            </Fragment>
        )
    }
}

export default Testimonial;
