import React, { Component } from 'react'
import AboutSection from '../components/AboutSection/AboutSection'
import BlogSection from '../components/BlogSection/BlogSection'
import CopyRight from '../components/CopyRightSection/CopyRight'
import FooterSection from '../components/FooterSection/FooterSection'
import HeroSlider from '../components/HeroSlider/HeroSlider'
import Menu from '../components/mainMenu/Menu'
import ServiceSection from '../components/ServiceSection/ServiceSection'
import Shedule from '../components/SheduleSection/Shedule'
import Testimonial from '../components/TestimonialSection/Testimonial'
import TopHeader from '../components/topheader/TopHeader'
import Video from '../components/VideoParallax/Video'
import VideoSection from '../components/VideoSection/VideoSection'

export class HomePages extends Component {
    componentDidMount(){
        window.scroll(0,0)
    }
    render() {
        return (
            <>
                <TopHeader title="Home Page"/>
                <Menu/>
                <HeroSlider/>
                <AboutSection/>
                <VideoSection/>
                <ServiceSection/>
                <Video/>
                <Shedule/>
                <Testimonial/>
                <BlogSection/>
                <FooterSection/>
                <CopyRight/>
            </>
        )
    }
}

export default HomePages
