import React, { Fragment, useState } from 'react';
import { Button, Col, Container, Modal, Row } from 'react-bootstrap';
import { FaPlayCircle } from 'react-icons/fa';
import ReactPlayer from 'react-player';
import '../../assets/css/style.scss';
import BG from '../../assets/images/Hero/sliderTwo.jpg';
function Video() {
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    return (
        <Fragment>
            <div className="VideoParallax" style={{backgroundImage:`url(${BG})`}}>
                <Container>
                    <Row className="justify-content-center">
                        <Col lg={6}>
                            <div className="VideoParSec">
                                <span><FaPlayCircle  onClick={handleShow}></FaPlayCircle></span>
                                {/* Moadl Start */}
                                <Modal show={show} onHide={handleClose}>
                                    <Modal.Body>
                                        <ReactPlayer url='https://www.youtube.com/watch?v=ysz5S6PUM-U' style={{maxWidth:"100%"}} />
                                    </Modal.Body>
                                    <Modal.Footer>
                                        <Button variant="secondary" onClick={handleClose}>  Close </Button>
                                        <Button variant="primary" onClick={handleClose}> Save Changes </Button>
                                    </Modal.Footer>
                                </Modal>
                                {/* Modal End */}
                                <h2>"Pray ! Listen the good adipisicing elit. Perspiciatis consequatur obcaecati accusamus?</h2>
                                <a href="#" className="BtnBTC">Donate Now</a>
                                <a href="#" className="BtnCTB">Read More</a>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </div>
        </Fragment>
    )
}

export default Video;
