import React, { Component, Fragment } from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { FcCheckmark } from 'react-icons/fc';
import '../../assets/css/style.scss';
import About1 from '../../assets/images/About/about1.jpg';
import About2 from '../../assets/images/About/about2.jpg';
export class AboutSection extends Component {
    render() {
        return (
            <Fragment>
                <div className="AboutSection">
                    <Container>
                        <Row className="justify-content-around align-items-center">
                            <Col lg={6} className="justify-self-center">
                                <div className="AboutImageSection">
                                    <img src={About2} className="AboutImageOne" alt="About Images2" />
                                    <img src={About1} className=" AboutImageTwo" alt="About Images" />
                                </div>
                            </Col>
                            <Col lg={5}>
                                <div className="AboutContent">
                                    <div className="SectionHeading">
                                        <p>About Us Section</p>
                                        <h2>You Can Help Children Just Like Money !</h2>
                                    </div>
                                    <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Ad, illo nobis. Explicabo, aperiam voluptatibus, qui aut fugit quos nostrum deleniti vero odio, in corrupti?</p>
                                    <ol>
                                        <li> <FcCheckmark/> 100% Trusted Website</li>
                                        <li><FcCheckmark/> Propper Education System</li>
                                    </ol>
                                    <a href="#" className="BtnBTC" >About More</a>
                                </div>
                            </Col>
                        </Row>
                    </Container>
                </div>
            </Fragment>
        )
    }
}

export default AboutSection;