import React, { Component, Fragment } from 'react';
import { FaRegArrowAltCircleLeft, FaRegArrowAltCircleRight } from 'react-icons/fa';
import Slider from "react-slick";
import '../../assets/css/style.scss';
import Slider3 from '../../assets/images/Hero/sliderFour.jpg';
import Slider1 from '../../assets/images/Hero/sliderThree.jpg';
import Slider2 from '../../assets/images/Hero/sliderTwo.jpg';
export class HeroSlider extends Component {
    render() {
        // Slider Custom Arrow
        function SampleNextArrow(props) {
            const { onClick } = props;
            return (
              <div className="NextArr" onClick={onClick} >
                    <FaRegArrowAltCircleRight/>
              </div>
            );
        }
        function SamplePrevArrow(props) {
            const { onClick } = props;
            return (
                <div className="PrevArr" onClick={onClick} >
                    < FaRegArrowAltCircleLeft/>
                </div>
            );
        }
        // Slider Settings
        var settings = {
            dots: false,
            infinite: true,
            speed: 800,
            autoplaySpeed: 9000,
            fade:true,
            autoplay:false,
            pauseOnHover:false,
            arrows:true,
            slidesToShow: 1,
            slidesToScroll: 1,
            nextArrow:<SampleNextArrow />,
            prevArrow: <SamplePrevArrow />
          };
        return (
            <Fragment>
                <Slider {...settings} className='heroSlider'>
                    <div>
                        <div className="sliderImg" style={{backgroundImage:`url(${Slider3})`}}>
                            {/* Frist Slide Content */}
                            <div className="HeroCont">
                                <span>Hope Is Alive</span>
                                <h1>Prossfer With Good Planning and Assets</h1>
                                <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Sint nostrum optio delectus corporis in.</p>
                                <a href="#" className="LreanMoreBTN">Learn More</a>
                            </div>
                        </div>
                    </div>
                    <div>
                        <div className="sliderImg" style={{backgroundImage:`url(${Slider1})`}}>
                            {/* Secound Slide Content */}
                            <div className="HeroCont">
                                <span>Grow your life</span>
                                <h1>Life With Good Planning and Assets</h1>
                                <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Sint nostrum optio delectus corporis in.</p>
                                <a href="#" className="LreanMoreBTN">Learn More</a>
                            </div>
                        </div>
                    </div>
                    <div>
                        <div className="sliderImg" style={{backgroundImage:`url(${Slider2})`}}>
                            {/* Third Slide Content */}
                            <div className="HeroCont">
                                <span>Role Of Sucsess</span>
                                <h1>More Than Life and Assets</h1>
                                <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Sint nostrum optio delectus corporis in.</p>
                                <a href="#" className="LreanMoreBTN">Learn More</a>
                            </div>
                        </div>
                    </div>
                </Slider>
            </Fragment>
        )
    }
}

export default HeroSlider;
